.wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.error{
    color: red;
    flex-direction: column;
    background: white;
    border: 1px solid red;
    border-radius: 10px;
    text-align: center;
}

.default{
    text-align: center;
    flex-direction: column;
    background: white;
    border-radius: 10px;

}